* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
}

.container {
  max-width: 1300px;
  width: 100%;
  margin: auto;
  padding: 0px 20px;
}
body {
  background-color: rgb(195, 190, 190);
  color: #111;
}
img {
  width: 80px;
  height: 80px;
}

.section {
  min-width: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu-tab {
  display: flex;
  justify-content: space-between;
  margin-bottom: 35px;
}

#searchInput {
  margin-top: 20px;
  width: 300px;
  height: 40px;
  margin-left: 450px;
  border-radius: 10px;
}
h1 {
  font-size: 50px;
  line-height: 60px;
  text-align: center;
  margin-bottom: 40px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card {
  flex: 0 0 calc(25% - 20px);
  width: 100%;
  background: #fff;
  border-radius: 10px;
  padding: 20px;
  margin-bottom: 20px;
  transition: 0.3s;
  border: 2px solid transparent;
}

.card:hover {
  border-color: #1d39c4;
}

.btn {
  border: none;
  outline: none;
  background-color: #1d39c4;
  color: #fff;
  border-radius: 10px;
  max-width: 120px;
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  cursor: pointer;
  transition: 0.3s;
  border: 1px solid #1d39c4;
}
.btn-menu {
  border: none;
  outline: none;
  background-color: #1d39c4;
  color: #fff;
  border-radius: 50px;
  max-width: 120px;
  width: 80%;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  cursor: pointer;
  transition: 0.3s;
  border: 1px solid #1d39c4;
}

.btn:hover {
  background-color: transparent;
  color: #1d39c4;
}

@media (max-width: 969px) {
  .section {
    padding: 20px 0px;
  }
  .card {
    flex: 0 0 calc(50% - 20px);
  }
  #searchInput {
    margin-top: 20px;
    width: 300px;
    height: 40px;
    margin-left: 260px;
    border-radius: 10px;
  }
  .btn-menu {
    border: none;
    outline: none;
    background-color: #1d39c4;
    color: #fff;
    border-radius: 50px;
    max-width: 80px;
    width: 80%;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    cursor: pointer;
    transition: 0.3s;
    border: 1px solid #1d39c4;
  }
}
@media (max-width: 600px) {
  .card {
    flex: 0 0 100%;
    max-width: 100%;
  }
  #searchInput {
    margin-top: 20px;
    width: 250px;
    height: 40px;
    margin-left: 0px;
    border-radius: 10px;
  }
  .btn-menu {
    border: none;
    outline: none;
    background-color: #1d39c4;
    color: #fff;
    border-radius: 50px;
    max-width: 120px;
    width: 80%;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    cursor: pointer;
    transition: 0.3s;
    border: 1px solid #1d39c4;
  }
  .menu-tab {
    display: flex;
    flex-direction: column;
  }
}
